  margin-top: 30px;
  margin-left: -60px;
  margin-right: -60px;
}
@media only screen and (max-width: 1279px) {
  .chart-slider {
    margin-left: -20px;
    margin-right: -20px;
  }
}
@media only screen and (max-width: 767px) {
  .chart-slider {
    padding-bottom: 30px;
  }
}
.chart-slider::before, .chart-slider::after {
  position: absolute;
  z-index: 3;
  content: "";
  width: calc(100% - 120px);
  height: 2px;
  background-color: rgba(255, 255, 255, 0.3);
  margin-left: 60px;
}
@media only screen and (max-width: 1279px) {
  .chart-slider::before, .chart-slider::after {
    width: calc(100% - 40px);
    margin-left: 20px;
  }
}
.chart-slider::before {
  top: 0;
}
.chart-slider::after {
  bottom: 0;
}
.chart-slider .swiper-nav .swiper-btn-next svg,
.chart-slider .swiper-nav .swiper-btn-prev svg {
  color: #ffffff;
}
.chart-slider .swiper-nav .swiper-btn-next:hover svg,
.chart-slider .swiper-nav .swiper-btn-prev:hover svg {
  color: rgba(255, 255, 255, 0.7);
}

@media only screen and (max-width: 991px) {
  .chart--1 .chart__wrapper {
    align-items: center;
  }
}
.chart--1 .chart__canvas {
  margin-top: 0;
}
@media only screen and (max-width: 1279px) {
  .chart--2 .chart__canvases {
    justify-content: center;
  }
}
@media only screen and (max-width: 767px) {
  .chart--2 .chart__canvases {
    flex-direction: column;
  }
}
.chart--2 .chart__canvas {
  position: relative;
}
@media only screen and (max-width: 991px) {
  .chart--2 .chart__canvas {
    margin-top: 10px;
  }
}
.chart--2 .chart__canvas:first-child {
  min-width: 180px;
  max-width: 180px;
  height: 180px;
  z-index: 1;
}
@media only screen and (max-width: 767px) {
  .chart--2 .chart__canvas:first-child {
    min-width: 220px;
    max-width: 220px;
    height: 220px;
  }
}
.chart--2 .chart__canvas:last-child {
  min-width: 380px;
  max-width: 380px;
  height: 320px;
}
@media only screen and (max-width: 767px) {
  .chart--2 .chart__canvas:last-child {
    min-width: 220px;
    max-width: 220px;
    height: 500px;
    margin-top: 0;
    padding-right: 10px;
  }
}
.chart--2 .chart__canvas-image img {
  width: 57%;
  height: 57%;
}
.chart--2 .chart__wrapper {
  gap: 50px;
}
@media only screen and (max-width: 1279px) {
  .chart--2 .chart__wrapper {
    flex-direction: column;
  }
}
@media only screen and (max-width: 991px) {
  .chart--2 .chart__wrapper {
    gap: 20px;
  }
}
.chart--2 .chart__col:first-child {
  width: calc(35% - 10px);
}
@media only screen and (max-width: 1279px) {
  .chart--2 .chart__col:first-child {
    width: 100%;
  }
}
.chart--2 .chart__col:last-child {
  width: calc(65% - 10px);
}
@media only screen and (max-width: 1279px) {
  .chart--2 .chart__col:last-child {
    width: 100%;
  }
}
.chart--3 .chart__canvas {
  position: relative;
  min-width: 520px;
  max-width: 520px;
  height: 320px;
}
@media only screen and (max-width: 991px) {
  .chart--3 .chart__canvas {
    order: 1;
    margin-top: 10px;
  }
}
@media only screen and (max-width: 767px) {
  .chart--3 .chart__canvas {
    min-width: 220px;
    max-width: 220px;
    height: 400px;
  }
}
.chart--3 .chart__wrapper {
  gap: 50px;
}
@media only screen and (max-width: 991px) {
  .chart--3 .chart__wrapper {
    gap: 20px;
  }
}
.chart--3 .chart__col:first-child {
  width: calc(35% - 10px);
}
@media only screen and (max-width: 991px) {
  .chart--3 .chart__col:first-child {
    width: 100%;
  }
}
.chart--3 .chart__col:last-child {
  width: calc(65% - 10px);
}
@media only screen and (max-width: 991px) {
  .chart--3 .chart__col:last-child {
    width: 100%;
  }
}
.chart--4 .chart__window {
  display: flex;
  flex-wrap: wrap;
  gap: 30px;
}
.chart--4 .chart__list-block {
  width: calc(50% - 15px);
}
@media only screen and (max-width: 767px) {
  .chart--4 .chart__list-block {
    width: 100%;
  }
}
.chart__window {
  padding: 30px 60px 20px;
  border-radius: 3px;
}
@media only screen and (max-width: 767px) {
  .chart__window {
    padding: 30px 20px;
  }
}
.chart__title {
  max-width: 670px;
  margin: 0 auto;
  color: #ffffff;
  font-weight: 700;
  font-size: 26px;
  text-align: center;
}
@media only screen and (max-width: 767px) {
  .chart__title {
    font-size: 24px;
  }
}
@media only screen and (max-width: 575px) {
  .chart__title {
    font-size: 20px;
  }
}
@media only screen and (max-width: 767px) {
  .chart__title {
    font-size: 18px;
  }
}
.chart__title--left {
  max-width: 100%;
  text-align: left;
}
.chart__text {
  color: #ffffff;
  font-size: 16px;
  margin-top: 20px;
}
.chart__text a {
  color: #ffffff !important;
  text-decoration: underline;
}
.chart__text a:hover {
  text-decoration: none;
}
.chart__wrapper {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  gap: 50px;
  margin-top: 40px;
}
@media only screen and (max-width: 991px) {
  .chart__wrapper {
    flex-direction: column;
    gap: 20px;
  }
}
@media only screen and (max-width: 767px) {
  .chart__wrapper {
    margin-top: 20px;
  }
}
.chart__col {
  display: flex;
  flex-direction: column;
}
@media only screen and (max-width: 991px) {
  .chart__col {
    align-items: center;
  }
}
.chart__description {
  color: #ffffff;
  font-size: 13px;
}
.chart__description--center {
  text-align: center;
}
.chart__description a {
  color: #ffffff;
  text-decoration: underline;
}
.chart__description a:hover {
  text-decoration: none;
}
.chart__description p + p {
  margin-top: 8px;
}
.chart__description p {
  color: #ffffff !important;
}
.chart__dashes {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border: 1px dashed #ffffff;
  border-left: 0;
  border-right: 0;
}
@media only screen and (max-width: 767px) {
  .chart__dashes {
    border-top: 0;
    border-bottom: 0;
    border-left: 1px dashed #ffffff;
    border-right: 1px dashed #ffffff;
    border-bottom: 1px dashed #ffffff;
  }
}
.chart__dashes::before, .chart__dashes::after {
  position: absolute;
  content: "";
  width: 100px;
  height: 1px;
  border: 1px dashed #ffffff;
  border-bottom: 0;
}
.chart__dashes::before {
  top: 46px;
  left: -68px;
  transform: rotate(-72deg);
}
@media only screen and (max-width: 767px) {
  .chart__dashes::before {
    top: -46px;
    left: -25px;
    transform: rotate(-59deg);
  }
}
.chart__dashes::after {
  bottom: 46px;
  left: -71px;
  transform: rotate(-113deg);
}
@media only screen and (max-width: 767px) {
  .chart__dashes::after {
    bottom: unset;
    left: unset;
    top: -47px;
    right: -28px;
    transform: rotate(-116deg);
  }
}
.chart__list {
  list-style: inside !important;
  margin: 20px 0 40px !important;
}
.chart__list li + li {
  margin-top: 10px;
}
.chart__list li {
  color: #ffffff;
  font-size: 14px;
}
.chart__canvases {
  display: flex;
  align-items: center;
}
.chart__canvas {
  position: relative;
  min-width: 340px;
  max-width: 340px;
  height: 340px;
  margin-top: 30px;
}
@media only screen and (max-width: 991px) {
  .chart__canvas {
    margin-bottom: 10px;
    order: -1;
  }
}
@media only screen and (max-width: 575px) {
  .chart__canvas {
    min-width: 220px;
    max-width: 220px;
    height: 220px;
  }
}
.chart__canvas-image {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  align-items: center;
  justify-content: center;
  width: 30%;
  height: 30%;
  border-radius: 50%;
  overflow: hidden;
}
.chart__canvas-image img {
  width: 75%;
  height: 75%;
  object-fit: contain;
  margin: 0 auto;
}
.chart__source {
  color: #ffffff;
  font-size: 11px;
  margin-top: 30px;
}
@media only screen and (max-width: 767px) {
  .chart__source {
    text-align: center;
  }
}

.hystmodal__wrap {
  padding: 0 15px;
}
.hystmodal__window {
  max-width: 600px;
  width: 100%;
  border-radius: 13px;
  overflow: hidden;
}
.hystmodal:before {
  opacity: 1;
  background-color: rgba(2, 31, 41, 0.2);
}
.hystmodal__close {
  position: absolute;
  top: 20px;
  right: 15px;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 30px;
  height: 30px;
  padding: 0;
  cursor: pointer;
  background-image: unset;
  background-size: unset;
}
.hystmodal__close:focus {
  outline: none;
  outline-offset: unset;
}
.hystmodal__close:hover svg {
  color: #30c2c6;
}
.hystmodal__close svg {
  color: #ffffff;
  transition: 0.3s ease;
}

.modal {
  display: flex;
  background-color: #30c2c6;
  color: #ffffff;
}
@media only screen and (max-width: 575px) {
  .modal {
    flex-direction: column;
  }
}
.modal__col {
  position: relative;
}
.modal__col:first-child {
  width: 52%;
  padding: 30px 15px 45px 30px;
}
@media only screen and (max-width: 575px) {
  .modal__col:first-child {
    width: 100%;
    padding: 45px 15px 20px 15px;
  }
}
.modal__col:last-child {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  gap: 12px;
  width: 48%;
  padding: 50px 22px 40px;
}
@media only screen and (max-width: 575px) {
  .modal__col:last-child {
    width: 100%;
  }
}
.modal__image {
  position: absolute;
  inset: 0;
  width: 100%;
  height: 100%;
}
.modal__image::after {
  content: "";
  position: absolute;
  inset: 0;
  background: rgba(41, 32, 49, 0.63);
  mix-blend-mode: multiply;
}
.modal__image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.modal__date, .modal__link {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 3px;
  border: 1px solid #ffffff;
  padding: 14px;
  font-weight: 500;
  font-size: 16px;
}
.modal__date {
  color: #ffffff;
}
.modal__link {
  color: #30c2c6;
  background-color: #ffffff;
  transition: 0.3s ease;
}
.modal__link:hover {
  border-color: transparent;
  background-color: rgba(255, 255, 255, 0.7);
}
.modal__type {
  font-weight: 500;
  font-size: 15px;
  text-transform: uppercase;
}
.modal__title {
  font-weight: 700;
  font-size: 24px;
  margin-top: 12px;
}
.modal__list {
  margin-top: 23px;
  list-style: inherit;
  padding-left: 15px;
}
@media only screen and (max-width: 575px) {
  .modal__list {
    margin-top: 10px;
  }
}
.modal__list li {
  font-weight: 500;
  font-size: 14px;
}